body {
  background-color: $wrapper-bg;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

sup {
  top: -0.3em;
}

.l-wrapper {
  @if $enable-fluid-layout {
    @extend .container-fluid;
  } @else {
    @extend .container;
  }

  &--boxed {
    @extend .container;
  }
}

.page-wrapper,
.card-block {
  @extend .card;
}

.page-header {
  @extend .card-header;

  h1 {
    margin-bottom: 0;
  }
}

.page-content,
.card-block {
  @extend .card-body;
}

.card-block {
  @extend .shadow;
}

.page-footer {
  @extend .card-footer;

  &:empty {
    display: none;
  }
}

//specific use
body:not(.page-index) .l-header {
  margin-bottom: $footer-margin-top;
}

//ACCOUNT
.page-wrapper--my-account {
  @extend .bg-white;
  @extend .border;
}


//CATEGORIES
#category #left-column {
  display: none;
  width: 0;
}

#category #content-wrapper {
  @extend .col-md-12;
}


.left-column > div,
.right-column > div {
  margin-bottom: $spacer;
}

/* HEADER */

@include media-breakpoint-down(sm) {
  .header__container {
    padding-left: 0;
    padding-right: 0;
  }
  .logo {
    width: 100%;
    max-width: 115px;
  }
}

//specific case of header (within card)
.header-bar {
  width: 80%;
  @media (min-width: map_get($grid-breakpoints, "md")) {
    width: 70%;
  }
}

.l-wrapper .display-1 {
  color: $gray-700;
}


.display-1 {
  position: relative;
  display: inline-flex;
  @extend .py-1;
  line-height: 2;

  &::before {
    content: "";
    margin: auto .42em auto 0;
    height: 80px;
    width: 0.22em;
    background: $gray-700;
  }
}


/* FOOTER */
.l-footer {
  background-color: $footer-bg;
  margin-top: $footer-margin-top;
  padding-top: $footer-padding-top;
}

/* MISC */
ul {
  list-style: none;
  padding-left: 0;
}


.quick-view {
  display: inline-block;
  @extend .small;
}

.form-group.form-group_forgot-password {
  margin-top: -($form-group-margin-bottom/2);
}

//touch spin

.bootstrap-touchspin {
  box-shadow: $bs-touchspin-boxshadow;
  border: $input-border-width solid $input-border-color;

  > input {
    max-width: 60px;
    border: none;
    box-shadow: none;
    text-align: center;
    font-weight: 700;
  }

  .btn {
    position: relative;
    z-index: 2;
    color: $input-color;
    background-color: $white;
  }

  .input-group-btn {
    display: flex;

    &:first-child {
      margin-right: -$input-border-width;
    }

    &:last-child {
      margin-left: -$input-border-width;
    }
  }
}

/* minimizing reflows and avoiding page jumps */

.rc {
  position: relative;
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 100%;
  background: rgba(0, 0, 0, 0.05) url("data:image/gif;base64,") no-repeat center center;
  transition: background 150ms;
}

.rc > * {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rc > a {
  display: block;
}

.ratio2_1 {
  padding-top: 50%;
}

.ratio1_2 {
  padding-top: 200%;
}

.ratio4_3 {
  padding-top: 75%;
}

.ratio16_9 {
  padding-top: percentage(9/16);
}

.ratio1_1 {
  padding-top: 100%;
}

.ratio3_2 {
  padding-top: percentage(1-2/3);
}

/* collapse icons */
.icon-collapse {
  transition: transform 0.4s ease;
}

.icon-collapse[aria-expanded="true"] {
  transform: rotateZ(180deg);
}

/* CART */

.mobile-cart .blockcart {
  padding-right: 1.5em;
}

.promo-code {
  margin-left: -$card-spacer-x;
  margin-right: -$card-spacer-x;
  padding: $card-spacer-x;
  background-color: $promo-code-bg;
}

.promo-code-alert {
  display: none;
}

.card-body .separator {
  margin-left: -$card-spacer-x;
  margin-right: -$card-spacer-x;
}

.cart-summary-line {
  display: flex;
  justify-content: space-between;
}

.media-list__item:not(:last-child) {
  margin-bottom: $spacer/2;
}

//cart summary
.cart-item:not(:last-child) {
  border-bottom: 1px solid $border-color;
  margin-bottom: $spacer;
  padding-bottom: $spacer;
}

.product-line-info {
  margin-bottom: $spacer/2;
}

/* CHECKOUT STEP */
.order-checkout .card-body {
  padding-bottom: 0;
}

.step-edit[aria-expanded="true"] {
  display: none;
}

.step-title {
  margin-bottom: 0;
}

.-complete.-reachable .step-title .done {
  display: inline-block;
}

.-current .step-title .done {
  visibility: hidden;
}

.-current .card-header {
  background-color: $white;
}

.accordion {
  .card-body .card {
    border: 1px solid $card-border-color;
    transition: border 0.1s linear;
  }

  .card-body .card-header {
    text-align: center;
  }

  .selected .card {
    border-color: $success;
    border-width: 2px;
  }
}

//Personnal info
.nav-tabs-info {
  font-size: $font-size-base * 1.1;

  .active {
    font-weight: bold;
  }
}

//Addresses

.address-selector {
  .card {
    transition: border 0.1s linear;
  }

  .card-header,
  .card-body {
    cursor: pointer;
  }
}

.card-body_add-address {
  padding: $card-spacer-y $card-spacer-x;

  .material-icons {
    opacity: 0.8;
  }
}

// SHipping
.carrier-price {
  font-weight: bold;
  font-size: $font-size-lg;
  color: $primary;
}

//order list final recap
.order-line:not(:last-child) {
  margin-bottom: $spacer/2;
}

.order-confirmation-table {
  padding: $spacer;
  border: 3px solid $card-border-color;
}

//prices
.regular-price {
  text-decoration: line-through;
  font-size: $regular-price-font-size;
  color: $regular-price-color;
  font-weight: 700;
}

.price {
  font-weight: 700;
  color: $price-color;
}

//forms
.custom-file-label.selected:after {
  content: "" !important;
}

.label {
  @extend label;
}

//nav tabs
.nav-tabs {
  .nav-link {
    color: $gray-600;
    font-weight: 700;
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    border-bottom-width: 3px;
  }
}

.tab-content {
  padding-top: $spacer;
}

//products-section-title
.products-section-title {
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin-bottom: $spacer;
  @extend .h1;
  font-weight: 700;
}

//home content spacing
.page-home > * {
  margin-bottom: $spacer * 0.75;
}

@include media-breakpoint-down(sm) {
  .modal-footer {
    flex-direction: column;
    flex-direction: column-reverse;

    & > .btn {
      margin: 0;
      width: 100%;
    }

    & .btn:not(:last-child) {
      margin-top: $spacer/2;
    }
  }
}

.nav-tabs {
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;

  &::-webkit-scrollbar {
    display: none;
  }

  .nav-link {
    white-space: nowrap;
  }
}

//forms
main > .notifications-container {
  @extend .l-wrapper;
}

.notifications-container {
  ul {
    margin-bottom: 0;
  }
}

.js-invalid-feedback-browser:empty {
  display: none;
}

.form-group {
  label,
  .label {
    small,
    .small {
      font-size: 0.75 * $font-size-base;
    }
  }
}

@include media-breakpoint-up(md) {
  .page-content,
  .checkout-form,
  .page-wrapper--order-confirmation {
    form {
      width: 75%;
      max-width: 450px;
      margin: 0 auto;
    }
  }
  .page-wrapper--order-detail {
    form {
      margin: unset;
    }
  }
}

.form-footer {
  margin-top: $spacer;
}

// badge order history
.badge.dark {
  color: #fff;
  mix-blend-mode: difference;
}


//init fix
//transitions

a {
  span,
  i {
    transition: color 0.4s $bezier;
  }

  &:hover span {
    color: $primary;
  }

  &:not(.account):hover {
    i {
      color: $primary;
    }
  }
}

:active, :focus {
  outline: none;
  -moz-outline-style: none;
}


//// link design && button
.btn-primary:disabled, .btn-primary:disabled i {
  color: $gray-300;
}

.btn-primary span, .btn-primary i, .btn-primary {
  color: $white;
}


.li-d, .cat-desc a {
  font-family: $font-family-base;
  clear: both;
  position: relative;
  overflow: hidden;
  padding-right:2rem;

  $initial-pd: 15px;
  $dis:1.5em;

  &::before,
  &::after {
    content: '\e5c8';
    position: absolute;
    right: 0;
    margin: auto 0;
    transition: .2s opacity $bezier2, .35s transform $bezier;
    margin-left:$initial-pd;
  }


  &::before {
    opacity: 0;
    @extend .material-icons;
    transform: translateX(-$dis) scale(0.5);
    padding-top: 2px;
  }


  &::after {
    content: '\e5c8';
    @extend .material-icons;
    transform: translateX(0) scale(1);
    padding-top: 2px;
  }


  &:hover {
    &::before {
      content: '\e5c8';
      transform: translateX(0) scale(1);
      opacity: 1;
    }

    &::after {
      content: '\e5c8';
      transform: translateX($dis) scale(0.5);
      opacity: 0;
    }
  }
}



//pagination product list
.pag small{
  font-weight: $font-weight-bold;
}